import { createWebHistory, createRouter } from "vue-router";
import Index from '../pages/index.vue'
import Topbar from '../layout/topbar.vue'
import Single from '../pages/single.vue'
import Listing from '../pages/list.vue'
import singleAgent from '../pages/agent-single.vue'
import addList from '../pages/add-list.vue'
import notFound from '../pages/404.vue'
import Dashboard from '../pages/admin/dashboard.vue'

const routes = [{
    path: '/',
    component: Topbar,
    children: [
        {
            path: '/',
            name: 'home',
            meta: {
                auth: false,
                admin: false,
                title: 'Нүүр'
            },
            component: Index
        },
        {
            path: '/single/:id',
            name: 'Property Single',
            meta: {
                auth: false,
                admin: false,
                title: 'Зарын дэлгэрэнгүй'
            },
            component: Single
        },
        {
            path: '/list',
            name: 'Listing',
            meta: {
                auth: false,
                admin: false,
                title: 'Жагсаалт'
            },
            component: Listing
        },
        {
            path: '/agent/:id',
            name: 'agent',
            meta: {
                auth: false,
                admin: false,
                title: 'Агент'
            },
            component: singleAgent
        },
        {
            path: '/add-list',
            meta: {
                auth: true,
                admin: false,
                title: 'Зар оруулах'
            },
            name: 'add-list',
            component: addList
        }]
    },
    {
        path: '/admin',
        component: Topbar,
        children: [
            {
                path: 'dashboard',
                meta: {
                    auth: true,
                    admin: true,
                    title: 'Dashboard'
                },
                name: 'admin-dashboard',
                component: Dashboard
            }
        ]
    },
    {
        path: '/:pathMatch(.*)*',
        meta: {
            auth: false,
            admin: false,
            title: 'Хуудас олдсонгүй'
        },
        name: 'page-not-found',
        component: notFound
    }
]

const router = createRouter({
    history: createWebHistory(),
    routes,
});

const auth = {
	loggedIn(admin) {
        console.log(admin);
		var token = localStorage.getItem("token");
		if (token) {
            // var dt = jwt_decode(token).expired;
            
            // if(dt > Date.now()) {
            // // if(dt > '2021-11-17 22:00:00') {
            //     return 'logged';
            // }
            // return 'expired';
            return 'logged'

		} else {
			return 'notlogged';
		}
	}
}

router.beforeEach((to, from, next) => {
    document.title = to.meta.title;
    let authrequired = false
    if(to && to.meta && to.meta.auth)
		authrequired = true
    
    if(authrequired) {
        const tk = auth.loggedIn(to.meta.admin);
        tk == 'logged' ? next() : router.push({ path: '/', query: { login: false }});
    } else {
        next();
    }
})

export default router